import {FloatingMessage} from "./styledComponents/FloatingMessage";
import {BuffBox} from "./styledComponents/BuffBox";
import {BuffTooltip} from "./styledComponents/BuffTooltip";
import ReactDOM from "react-dom";
import React from "react";
export class Character{
    gameinfo;
    constructor(gameinfo) {
        this.gameinfo = gameinfo;
    }
    name = ""
    baseHp = 0
    star = 0
    code = 0
    info = ""
    level = 0
    passive = false;
    passiveinfo(){

    }
    skill1 = false
    skill1name = ""
    skill1info(){

    }
    skill1imgsrc = ""
    skill1avail = false;
    skill2 = false
    skill2name = ""
    skill2info(){

    }
    skill2imgsrc = ""
    skill2avail = false
    shield = false
    baseShield = 0;
    imgsrc = ""
    normalAttack(){

    }
    chargedAttack(){

    }
    skill1Attack(){

    }
    skill2Attack(){

    }
    afterEnemyAttack(){

    }
    updateginfo(gameinfo){
        this.gameinfo = gameinfo;
    }
    findCharacter(code,level){
        switch (code){
            case "c4001":
                return new Satania(level,this.gameinfo);
            case "c4002":
                return new Gabriel(level,this.gameinfo);
            case "c4003":
                return new Salazar(level,this.gameinfo);
            case "c4004":
                return new Elfa(level,this.gameinfo);
            case "c4005":
                return new Kazama(level,this.gameinfo);
            case "c4006":
                return new RobinHood(level,this.gameinfo);
            default:
                return null;
        }
    }
}
class Satania extends Character{
    level = 0;
    ginfo;
    baseHp = 250;
    constructor(Level,ginfo) {
        super();
        this.level = Math.min(Level,5);
        this.ginfo = ginfo;
        this.baseHp = 250 + 35*(this.level-1);
        //console.log(this.ginfo)
    }
    name = "Satania san";
    star = 4;
    code = "c4001";
    info = "Satanicia Kurumizawa McDowell. The future queen of hell arrives!";
    passive = true;
    passiveinfo(Level){
        return <p>Intimidation: If enemy has lower HP than Satania, increase normal attack damage by <span className="blue-text">{15 + 2.5*(Level-1)}%</span>.</p>
    }
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1000824750140051486/fdc2a76dbc40c91aeaa5f0dd46fa20de.jpg";
    normalAttack(){
        //console.log(this.ginfo)

        setTimeout(()=>{
            let hitDmg = this.ginfo.heroDamage + Math.floor((Math.random()-0.5)*(this.ginfo.heroDamage/10));
            if(this.ginfo.enemyHp < this.ginfo.heroHp){
                hitDmg = Math.floor(hitDmg*(1.15+0.025*(this.level-1)));
                //applying buff
                const heroBuff = document.querySelector("#heroBuff");
                if(heroBuff.querySelector("#SataniaIntimidation")===null){
                    let newnode = document.createElement("div");
                    newnode.id = "SataniaIntimidation";
                    newnode.innerHTML = `<div class="buffBox" style='background-image: url("https://media.discordapp.net/attachments/1004035613684682812/1006580339189235843/satania_intimidation_passive.jpg?width=497&height=604")'>
                            
                    </div>
                    <div class="buffTooltip">
Satania deals extra normal damage.
                    </div>`
                    newnode.firstChild.addEventListener("mouseenter",()=> {
                            document.querySelector("#SataniaIntimidation .buffTooltip").style.opacity = "100%";
                        }
                    )
                    newnode.firstChild.addEventListener("mouseout",()=>{
                        document.querySelector("#SataniaIntimidation .buffTooltip").style.opacity = "0%";
                    })
                    heroBuff.appendChild(newnode);
                }
            }else{
                const heroBuff = document.querySelector("#heroBuff");
                if(heroBuff.querySelector("#SataniaIntimidation")!==null){
                    heroBuff.removeChild(heroBuff.querySelector("#SataniaIntimidation"));
                }
            }
            this.ginfo.setEnemySideText(<> </>);
            if(this.ginfo.enemyShield){
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
                this.ginfo.setEnemyShieldHp(prevState => {
                    return prevState - hitDmg
                })
            }else {
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="green-text">-{hitDmg} Health</span></FloatingMessage></>)
                this.ginfo.setEnemyHp(prevState => {
                    return prevState - hitDmg
                })
            }
        },0)

    }
    chargedAttack(){
        setTimeout(()=>{
            const heroBuff = document.querySelector("#heroBuff");
            if(heroBuff.querySelector("#SataniaIntimidation")!==null){
                heroBuff.removeChild(heroBuff.querySelector("#SataniaIntimidation"));
            }
            let chance = Math.random();
            if(chance > 0.5){
                let hitDmg = this.ginfo.heroDamage + Math.floor((Math.random()-0.5)*(this.ginfo.heroDamage/10));
                this.ginfo.setEnemySideText(<> </>);
                if(this.ginfo.enemyShield){
                    this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{2 * hitDmg} Shield</span></FloatingMessage></>)
                    this.ginfo.setEnemyShieldHp(prevState => {
                        return prevState - 2 * (hitDmg)
                    })
                }else {
                    this.ginfo.setEnemySideText(<><FloatingMessage><span className="green-text">-{2 * hitDmg} Health</span></FloatingMessage></>)
                    this.ginfo.setEnemyHp(prevState => {
                        return prevState - 2 * (hitDmg)
                    })
                }
            }else{
                this.ginfo.setEnemySideText(<> </>);
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="red-text">Missed</span></FloatingMessage></>)
            }
        },0)
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
    }

}
